@import "./variables";

@mixin squareSize($size) {
  width: $size;
  height: $size;
}

@mixin pageGrid {
  --columns: 24;
  display: grid;
  column-gap: 16px;
  grid-template-columns: repeat(var(--columns), 1fr);
}

@mixin sectionTitle {
  font-size: 32px;
  font-weight: 700;
  line-height: 41px;
  color: $primary-color;
}

@mixin sectionTitle_H1 {
  @include sectionTitle;

  letter-spacing: -0.01em;
}

@mixin sectionTitle_H2 {
  @include sectionTitle;

  font-size: 22px;
  line-height: 31px;
}

@mixin sectionTitle_H3 {
  @include sectionTitle;

  font-size: 20px;
  font-weight: 600;
  line-height: 26px;
}

@mixin bodyText {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: $primary-color;
}

@mixin buttonsText {
  @include bodyText;

  font-weight: 600;
}

@mixin smallText {
  @include bodyText;

  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: $secondary-color;
}

@mixin upperCaseText {
  @include bodyText;

  font-weight: 700;
  font-size: 12px;
  line-height: 11px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

@mixin navigationLink {
  @include upperCaseText;

  position: relative;

  color: $secondary-color;
  text-decoration: none;
}

@mixin navigationUnderline {
  color: $primary-color;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    height: 3px;
    width: 100%;

    display: block;
    background-color: $primary-color;
  }
}

@mixin logo {
  height: 100%;
  width: 40px;

  background: no-repeat center url(../../helpers/icons/icon_logo.svg);
}
